  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.alinear-derecha {
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .alinear-derecha {
    display: flex;
    justify-content: flex-end;
  }
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.w-100 {
  width: 100%;
}

.header {
  background-color: #333333;
  padding: 2.5rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.header.inicio {
  background-image: url(../img/header.jpg);
  background-size: cover;
}
.header.inicio h1 {
  color: #FFFFFF;
  font-weight: bold;
  max-width: 60rem;
  text-align: left;
}
.header.inicio .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.footer {
  background-color: #333333;
  padding-top: 2.5rem;
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer p {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 0;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    gap: 2rem;
  }
}

.mobile-menu {
  width: 5rem;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.sobre-nosotros {
  text-align: center;
  border-bottom: 0.2rem solid #e1e1e1;
  margin-bottom: 2rem;
}
.sobre-nosotros img {
  height: 10rem;
}

@media (min-width: 768px) {
  .contenido-main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

@media (min-width: 768px) {
  .sobre-nosotros {
    border-bottom: none;
  }
}

@media (min-width: 768px) {
  .casas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .casas .tercero h2 {
    min-height: 12.238rem;
  }
}

.columna {
  background-color: #eeeeee;
  border: 0.1rem solid #d4d4d4;
  margin-bottom: 2rem;
}
.columna .iconos-casas {
  display: flex;
  justify-content: space-between;
  padding: 0;
  font-weight: bold;
  gap: rem;
  max-width: 40rem;
}
.columna .iconos-casas li {
  display: inline-flex;
}
.columna .iconos-casas li p {
  margin-left: 1.5rem;
}

.iconos-casas {
  display: flex;
  justify-content: space-between;
  padding: 0;
  font-weight: bold;
  gap: rem;
  max-width: 40rem;
}
.iconos-casas li {
  display: inline-flex;
}
.iconos-casas li p {
  margin-left: 1.5rem;
}

.descripcion {
  padding: 2rem;
}

.precio {
  color: #71B100;
  font-size: 3.5rem;
  margin: 0;
  font-weight: bold;
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
  margin: 0;
  width: 100%;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
  margin: 0;
  width: 100%;
}

.imagen-contacto {
  background-size: cover;
  background-position: center center;
  padding: 5rem 0;
  text-align: center;
  color: #FFFFFF;
  max-width: unset;
}
.imagen-contacto p {
  color: #FFFFFF;
  font-family: Lato, sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}

@media (min-width: 768px) {
  .entrada {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}

.entrada picture {
  margin-top: 1.5rem;
}
.entrada h3 {
  margin-top: 0;
  font-weight: bold;
  text-align: left;
  color: #000000;
}
.entrada h3:after {
  display: block;
  width: 20rem;
  content: "";
  color: #71B100;
  border: 0.25rem solid #71B100;
}
.entrada span {
  color: #E08709;
}

.testimoniales {
  position: relative;
  background-color: #71B100;
  padding: 3rem 2rem 2rem 6rem;
  border-radius: 2rem;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.testimoniales blockquote {
  font-size: 2.5rem;
}
.testimoniales blockquote:before {
  content: "";
  position: absolute;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  width: 6rem;
  height: 8rem;
  left: 4rem;
}
.testimoniales p {
  color: #FFFFFF;
  text-align: end;
}

.contenedor span {
  color: #E08709;
}

.boton {
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  text-decoration: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  font-weight: bold;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-verde {
  background-color: #71B100;
  text-decoration: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  font-weight: bold;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-amarillo {
  background-color: #E08709;
  text-decoration: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  font-weight: bold;
  border: none;
  margin: 4rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

@media (min-width: 768px) {
  .boton-verde {
    background-color: #71B100;
    text-decoration: none;
    display: inline-block;
    text-align: center;
    color: #FFFFFF;
    padding: 1.5rem 2rem;
    font-weight: bold;
    border: none;
  }
  .boton-verde:hover {
    background-color: #619800;
  }
}

@media (min-width: 768px) {
  .boton-amarillo {
    background-color: #E08709;
    text-decoration: none;
    display: inline-block;
    text-align: center;
    color: #FFFFFF;
    padding: 1.5rem 2rem;
    font-weight: bold;
    border: none;
  }
  .boton-amarillo:hover {
    background-color: #c77808;
  }
}

.boton-rojo-block {
  background-color: rgba(255, 0, 0, 0.473);
  text-decoration: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  font-weight: bold;
  border: none;
  margin: 1rem 0;
}
.boton-rojo-block:hover {
  background-color: rgba(230, 0, 0, 0.473);
}

.boton-amarillo-block {
  background-color: #E08709;
  text-decoration: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  font-weight: bold;
  border: none;
  margin: 1rem 0;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.img-nosotros {
  margin-top: 2rem;
}

.texto-nosotros h3 {
  margin-top: 0;
  font-weight: bold;
  text-align: left;
}

.formulario label {
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.formulario input:not([type=submit]),
.formulario select,
.formulario textarea {
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  padding: 1rem;
  border: 0.1rem solid #e1e1e1;
  margin-bottom: 2rem;
}
.formulario p {
  font-size: 1.6rem;
}
.formulario textarea {
  min-height: 20rem;
}
.formulario input[type=submit] {
  margin-top: 2rem;
  display: block;
}

.forma-contacto {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

.darkmode {
  background-color: #1a1a1a;
}
.darkmode p, .darkmode h1, .darkmode h2, .darkmode h3, .darkmode h4 {
  color: #e1e1e1;
}
.darkmode .columna {
  background-color: #333333;
  border: 1px solid #333333;
}
.darkmode .columna ul img {
  filter: invert(100%);
}
.darkmode .formulario legend, .darkmode .formulario label {
  color: #e1e1e1;
}
.darkmode .formulario input:not([type=submit]), .darkmode .formulario textarea {
  background-color: #e1e1e1;
}

.alerta {
  padding: 1rem 2rem;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: bold;
  display: inline-block;
}
.alerta.error {
  background-color: rgb(167, 30, 30);
}
.alerta.exito {
  background-color: #71B100;
  display: block;
}

table.propiedades {
  margin-top: 2rem;
  width: 100%;
  border-spacing: 0;
  color: #FFFFFF;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  padding: 1rem 1.5rem;
}
table.propiedades tbody {
  color: black;
  text-align: center;
}
table.propiedades tbody img {
  width: 20rem;
  display: unset;
  align-items: center;
}

.imagen-propiedad {
  width: 20rem;
}

.imagen-anuncio {
  max-width: 85rem;
  text-align: center;
  margin: auto;
}